import './App.css';
import React from 'react';
import ThreeCanvas from './scripts/ThreeCanvas';
import gif from './images/marchingcube.gif';


class Introduction extends React.Component{
  render(){
    return (
      <section id="intro-section">
        <h1 id="title">Marching Cubes: A Quick Note</h1>
        <div id="intro-paragraph">
        <p>
          Awhile back, I saw a YouTube video titled Coding Adventure: Marching Cubes by Sebastian Lague. It was an exploration of a graphical algorithm which generates a mesh (shape) based of 8-point fields (cubes). For those curious, the video goes in-depth about how the algorithm generates the mesh, but essentially:
        </p>
        <ol className="style-list">
            <li>Take 8 equidistant points, forming a cube.</li>
            <li>Assign a numeric value to the points, based on a formula of your choosing.</li>
            <li>Compare the numeric values to a constant, called the isolevel.</li>
            <li>Create a new byte, set it to 0.</li>
            <li>Go through the points, and if the points value is above the isolevel, set the appropriate bit in the byte to 1. For example, if point 1s value is 25, and the isolevel is 10, then the byte is now 0000 0001.</li>
            <li>As such, the byte creates a number between 0 and 255.</li>
            <li>By inputting this number into a premade array of possible combinations, you get a shape made of triangles!</li>
            <li>You can use vertex interpolation to smooth out the shape.</li>
            <li>Finally, take these triangles and render them on the screen.</li>
          </ol>
          <p>
          If some of you are confused, thats perfectly normal. My brain shutdown the first time I read the algorithm. Take your time on each step, and dont be afraid to illustrate it on paper or whiteboard. Feel free to explore the demo on this page: its made to illustrate what is possible with marching cubes and a bit of tinkering with the formula.
          As a final note, if you are interested in implementing the algorithm, these are the sources I used:
          </p>
          <ul>
            <li><a href="http://paulbourke.net/geometry/polygonise/"> Paul Bourke's Marching Cube Implementation</a></li>
            <li><a href="https://www.youtube.com/watch?v=M3iI2l0ltbE"> Sebastian Lague's Video On Marching Cubes</a></li>
          </ul>
        </div>
        <img id="showcase-image" src={gif} alt="Marching Squares Showcase!"></img>
      </section>
    )
  }
}

class Card extends React.Component{
  render(){
    return (
      <div className="card">
        <h3>{this.props.name}</h3>
        <p>{this.props.description}</p>
      </div>
    );
  }
}

class Options extends React.Component{
  constructor(props){
      super(props);
      this.handleClick = this.handleClick.bind(this);
      this.defaults = {
        formula: "vectorx + vectory + vectorz",
        isolevel: 1,
        totalSize: 200,
        individualSize: 10,
        showGrid: false,
        showVertices: false
      }
  }

  sanitize(rawInputs){
      for(const key in rawInputs)
        if(!rawInputs || rawInputs[key].length === 0)
          rawInputs[key] = this.defaults[key];
      
      rawInputs.formula = rawInputs.formula.toLowerCase();
      rawInputs.isolevel = parseFloat(rawInputs.isolevel);
      rawInputs.totalSize = parseFloat(rawInputs.totalSize);
      rawInputs.individualSize = parseFloat(rawInputs.individualSize);
  }

  handleClick(event){
    event.preventDefault();

    let inputs = {
      formula: document.querySelector('#formula-input').value,
      isolevel: document.querySelector('#isolevel-input').value,
      totalSize: document.querySelector('#totalSize-input').value,
      individualSize: document.querySelector('#individualSize-input').value,
      showGrid: document.querySelector('#showGrid-input').checked,
      showVertices: document.querySelector('#showVertices-input').checked
    }
    this.sanitize(inputs);
    this.props.regenerate(inputs.formula, inputs.isolevel, inputs.totalSize, inputs.individualSize, inputs.showGrid, inputs.showVertices);
  }

  render(){
      return (
          <div className="row-wrapper" id="options-container">
            <h2>Options</h2>
            <form className="row-column" onSubmit={this.handleClick}>
              <div>
                <label htmlFor="showGrid">Show Cube Grid</label>
                <input type="checkbox" className="checkbox-input" id="showGrid-input" name="showGrid"></input>
              </div>
              <div>
                <label htmlFor="showGrid">Show Generated Vertices</label>
                <input type="checkbox" className="checkbox-input" id="showVertices-input" name="showVertices"></input>
              </div>
              <input id="individualSize-input" name="individualSize" type="number" min="0" placeholder={`Cube Size: ${this.defaults.individualSize}`}></input>
              <input id="totalSize-input" name="totalSize" type="number" min="0" placeholder={`Total Size: ${this.defaults.totalSize}`}></input>
              <input id="isolevel-input" name="isolevel" type="number" placeholder={`Isolevel: ${this.defaults.isolevel}`}></input>
              <label htmlFor="formula">Formula</label>
              <input id="formula-input" name="formula" type="text" placeholder={`${this.defaults.formula}`}></input>
              <button id="generate-button" type="submit">Generate</button>
              <p id="error-paragraph"></p>
            </form>
            <h2>Available Variables</h2>
            <div className="card-container">
              <Card name="Vector" description="Represents a point's placement in the world. X is vectorx, Y is vectory and Z is vectorz."></Card>
              <Card name="Length" description={`Represents the point's euclidian length. Try adding "length" into an expression!`}></Card>
              <Card name="Noise" description={`Represents a noise-generated value associated to the point. Typing "noise" will add randomness to the equation!`}></Card>
            </div>
          </div>
      );
  }
}

class TCanvas extends React.Component{
  componentDidMount(){
      this.mount.appendChild(this.props.canvas.renderer.domElement);
  }

  render(){
      return (
        <div id="canvas-container" ref={ref => (this.mount = ref)}></div>
      )
  }
}

class Demo extends React.Component{
  constructor(props){
    super(props);
    this.state = {canvas: new ThreeCanvas(window.innerWidth, window.innerHeight)}
    this.resizeCanvas = this.resizeCanvas.bind(this);
    window.onresize = this.resizeCanvas;
  }

  resizeCanvas(){
    this.state.canvas.resize(window.innerWidth, window.innerHeight)
  }

  render(){
    return(
      <section id="demo-section">
        <Options regenerate={this.state.canvas.generate.bind(this.state.canvas)}></Options>
        <TCanvas canvas={this.state.canvas}></TCanvas>
      </section>
    )
  }
}

class App extends React.Component{
  render(){
    return(
      <main>
        <Introduction></Introduction>
        <Demo></Demo>
      </main>
    )
  }
}

export default App;
